import React from 'react';
import classes from './subjects.module.css';
import SubCards from './subCards/subCards';

import subjectImg1 from '../../images/subjects1.jpg';
import subjectImg2 from '../../images/subjects2.png';
import subjectImg3 from '../../images/subjects3.jpg';
import subjectImg4 from '../../images/subjects4.png';
import subjectImg5 from '../../images/subjects5.jpg';

const subjects =() =>{

    return(
        <div className={classes.subjects}>
            <div className={classes.subjects_main}>
                <div className={classes.subjects_row}>
                    <SubCards
                        name="PDS Theory"
                        code="CS10001"
                        credits="3"
                        img={subjectImg1}
                        url="pds" />
                    <SubCards
                        name="Physics"
                        code="PH11001"
                        credits="4"
                        img={subjectImg2}
                        url="physics" />                                        
                </div>

                <div className={classes.subjects_row}>
                <SubCards
                        name="Economics"
                        code="HS20001"
                        credits="4"
                        img={subjectImg3}
                        url="economics" />
                </div>
                
                <div className={classes.subjects_row}>
                    <SubCards
                        name="Chemistry"
                        code="CY11001"
                        credits="4"
                        img={subjectImg4}
                        url="chemistry" />                
                    <SubCards
                        name="Thermodynamics"
                        code="ME22002"
                        credits="4"
                        img={subjectImg5}
                        url="thermodynamics" />
                </div>
            </div>
        </div>
        
    )
};

export default subjects;