import React,{Component} from 'react';
import classes from './participant.module.css';

class participant extends Component{

	state={
		show:true
	};

	removeHandler=()=>{
		this.setState({show:false});
	}
	render(){
		let part=null;
		if( this.state.show)
			part=(
		 	<div className={classes.participant_data}>
				<div>{this.props.name}</div>
				<div>{this.props.roll}</div>
				<div className={classes.remove} onClick={this.removeHandler}>
					<div></div>
				</div>
			</div>
		)
		return(
			<div className={classes.participant}>{part}</div>

		);
	}
}; 

export default participant;