import React,{Component} from 'react';
import Participants from '../participants';
import classes from './economics.module.css';
import AddBtn from '../addbtn/addbtn';

class economics extends Component{
	state={
		participants:[
						['Sahil Kumar', '19CH30024'],
						['Anish Das', '19CE30024'],
						['Apurv Anand', '19EE30024'],
						['Abhideep Khare', '19HS10024'],
						['Anuj Singh', '19EC30021'],
						['Gargi Das', '19GG30023'],
					 ]
	}
	render(){
		return(
			<div className={classes.economics}>
				<div className={classes.head}>
					<h2>ECONOMICS</h2>
					<h4>HS20001</h4>
				</div>

				<Participants studs={this.state.participants} />
				<AddBtn />
			</div>
		);
	}
}

export default economics;