import React,{Component} from 'react';
import Participants from '../participants';
import classes from './chemistry.module.css';
import AddBtn from '../addbtn/addbtn';

class chemistry extends Component{
	state={
		participants:[
						['Raghav Sharma', '19AG30024'],
						['Rishabh Aggarwal', '19NA30024'],
						['Sreyan Biswas', '19EC30024'],
						['Praveen Yadav', '19ME10024'],
						['Aditya Verma', '19GG30021'],
						['Kashish Lundia', '19PH30023'],
					 ]
	}

	render(){
		return(
			<div className={classes.chemistry}>
				<div className={classes.head}>
					<h2>CHEMISTRY</h2>
					<h4>CY11001</h4>
				</div>

				<Participants
				 	studs={this.state.participants}
					remove={this.removeHandler}  />
				<AddBtn />
			</div>
		);
	}
}

export default chemistry;