import React,{Component} from 'react';

import classes from './participants.module.css';
import Participant from './participant/participant';




class Participants extends Component{
	render(){
		console.log(this.props.studs[0][2]);

		return(
			<div className={classes.participants}>
				<Participant 
					name={this.props.studs[0][0]}
					roll={this.props.studs[0][1]}
					show={this.props.studs[0][2]}
					clicked={()=> this.props.remove(0)} />
				<Participant 
					name={this.props.studs[1][0]}
					roll={this.props.studs[1][1]}
					show={this.props.studs[1][2]}
					clicked={()=> this.props.remove(1)} />
				<Participant 
					name={this.props.studs[2][0]}
					roll={this.props.studs[2][1]}
					show={this.props.studs[2][2]}
					clicked={()=> this.props.remove(2)} />
				<Participant 
					name={this.props.studs[3][0]}
					roll={this.props.studs[3][1]}
					show={this.props.studs[3][2]}
					clicked={()=> this.props.remove(3)} />
				<Participant 
					name={this.props.studs[4][0]}
					roll={this.props.studs[4][1]}
					show={this.props.studs[4][2]}
					clicked={()=> this.props.remove(4)} />
				<Participant 
					name={this.props.studs[5][0]}
					roll={this.props.studs[5][1]}
					show={this.props.studs[5][2]}
					clicked={()=> this.props.remove(5)} />
			</div>
			);
	}
}

export default Participants;