import React,{Component} from 'react';
import Participants from '../participants';
import classes from './physics.module.css';
import AddBtn from '../addbtn/addbtn';

class physics extends Component{
	state={
		participants:[
						['Keshav Sharma', '19EE30024'],
						['Satvik Bansal', '19CS30024'],
						['Somil Jain', '19EC30024'],
						['Chetan Gowda', '19ME10024'],
						['Aaryan Garg', '19GG30021'],
						['Muskan Jain', '19CH30023'],
					 ]
	}
	render(){
		return(
			<div className={classes.physics}>
				<div className={classes.head}>
					<h2>PHYSICS</h2>
					<h4>PH11001</h4>
				</div>

				<Participants studs={this.state.participants} />
				<AddBtn />
			</div>
		);
	}
}

export default physics;