import React from 'react';
import classes from './subCards.module.css';
import {Link} from 'react-router-dom';

const subCard = (props) =>(
    <Link to={"/E-class-monitor/subjects/" +props.url} className={classes.card}>
        <img src={props.img} alt="" className={classes.card_img}/>
        <div>
            <span className={classes.card_prop}>Subject:</span>
            <span className={classes.card_value}>{props.name}</span>
        </div>

        <div>
            <span className={classes.card_prop}>Subject Code:</span>
            <span className={classes.card_value}>{props.code}</span>
        </div>

        <div>
            <span className={classes.card_prop}>Credits:</span>
            <span className={classes.card_value}>{props.credits}</span>
        </div>
    </Link>
);

export default subCard;