import React,{Component} from 'react';
import {Route, Switch} from 'react-router-dom';
import './App.css';

import Layout from './components/layout/layout';
import Home from './components/home/home';
import Subjects from './components/subjects/subjects';

import Chemistry from './components/participants/chemistry/chemistry';
import Economics from './components/participants/economics/economics';
import Thermodynamics from './components/participants/thermodynamics/thermodynamics';
import Pds from './components/participants/pds/pds';
import Physics from './components/participants/physics/physics';

class App extends Component
{
	render(){
		return(
		<div>
			<Layout>
				<Switch>
					<Route path="/E-class-monitor/" exact component={Home} />
					<Route path="/E-class-monitor/subjects" exact component={Subjects} />
					<Switch>
						<Route path='/E-class-monitor/subjects/chemistry' component={Chemistry} />
						<Route path='/E-class-monitor/subjects/economics' component={Economics} />
						<Route path='/E-class-monitor/subjects/thermodynamics' component={Thermodynamics} />
						<Route path='/E-class-monitor/subjects/pds' component={Pds} />
						<Route path='/E-class-monitor/subjects/physics' component={Physics} />
					</Switch>
				</Switch>
			</Layout>
		</div>
		)
	};
}

export default App;
	