import React from 'react';
import Aux from '../../../hoc/Auxil';
import Backdrop from '../../../UI/Backdrop/backdrop';
import classes from './sidedrawer.module.css';
import {Link} from 'react-router-dom';


const SideDrawer=(props)=>{

    let classList = [classes.SideDrawer, classes.Close];
    if(props.open){
        classList= [classes.SideDrawer, classes.Open];
    }

    return(
        <Aux>
            <Backdrop 
                show={props.open}
                clicked={props.closed} />
            <div className={classList.join(' ')}>
                <div className={classes.Logo}>E-CLASS MANAGER</div>
                <span className={classes.nav_list}>
                    <Link to="/E-class-monitor/" className={classes.nav_item}>Home</Link>
                    <Link to="/E-class-monitor/subjects" className={classes.nav_item}>Subjects</Link>
                </span>
            </div>
        </Aux>
    )
}

export default SideDrawer;