import React from 'react';
import classes from './toolbar.module.css';
import {Link} from 'react-router-dom';

const toolbar =(props) => (
    <header className={classes.Toolbar}>
        <div className={classes.logo}>E-CLASS MANAGER</div>
        <div className={classes.nav_items}>
            <Link to="/E-class-monitor/" className={classes.nav_item}>Home</Link>
            <Link to="/E-class-monitor/subjects" className={classes.nav_item}>Subjects</Link>
        </div>
        
    </header>
)

export default toolbar;