import React,{Component} from 'react';
import Participants from '../participants';
import classes from './pds.module.css';
import AddBtn from '../addbtn/addbtn';

class pds extends Component{
	state={
		participants:[
						['Aditya Aggarwal', '19CH30024'],
						['Ishan Goel', '19EC30024'],
						['Ashish Gupta', '19EE30024'],
						['Abhisek Kumar', '19ME10024'],
						['Tushar Mahawar', '19MI30021'],
						['Khushboo Bhoy', '19EE10023'],
					 ]
	}
	render(){
		return(
			<div className={classes.pds}>
				<div className={classes.head}>
					<h2>PDS THEORY</h2>
					<h4>CS11001</h4>
				</div>

				<Participants studs={this.state.participants} />
				<AddBtn />
			</div>
		);
	}
}

export default pds;